import inputs from '@/components/QuShe-inputs/inputs.vue';
import uniNumberBox from "@/components/uni-number-box/uni-number-box.vue";
import uniRequest from 'uni-request';
export default {
	data() {
		return {
			goodsItem:{},
			desc:'',
			count:1,
			textareaValue:'请说明退换货原因',
			type:1,
			blob:{},
			imgList: [],
			modalName: null,
			index:-1,
			orderId:null,
			radioItems: [{
					value: '退货',
					checked: 'true',
					type:1,
					
				},
				{
					value: '换货',
					type:2,
				}
			],
			inputsArray: [
				{
					segmentationTitle: "上传图片",
					type: "pics",
					title: "上传图片",
					itemArray: [{
						title: "图片1",
						ignore: true
					}, {
						title: "图片2",
						ignore: true
					}],
					variableName: "pic",
					border_top: "1px solid #f2f2f2",
					clearColor: "#c0ebd7",
					customId: "自定义一标识"
				}
			]
		}
	},
	components:{uniNumberBox,inputs},
	onLoad(options){
		console.log(options.item)
		console.log(JSON.parse(options.item))
		this.goodsItem = JSON.parse(options.item)
		console.log(this.goodsItem)
		this.orderId = options.order_id
		console.log(this.orderId)
	},
	methods: {
		blur(e){
			console.log(e)
			this.desc=e.detail.value
		},
		
		ChooseImage() {
			uni.chooseImage({
				count: 4, //默认9
				sizeType: ['compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					// this.blob = res
					console.log(res)
					//blob数据转file
					this.imgList = this.imgList.concat(res.tempFilePaths)
					
					/* let imgs = this.imageList.map((value, index) => {
					    return {
					            name: "image" + index, 
					            uri: value
					        }
					}); */
					console.log(this.imgList)
					uni.uploadFile({
						url: 'http://192.168.1.181:8000/api/order/create/return/goods/user/', //仅为示例，非真实的接口地址
						filePath: this.imgList[0],
						name:'image1',
						header: {
							token:'JWT '+uni.getStorageSync('userInfo').token,
							"Content-Type": "multipart/form-data"
						},
						
						success: (uploadFileRes) => { 
							resolve(uploadFileRes.data);  
						}
					});
					/* formData: {
						'image1': this.imgList[0]
					}, */
					// this.urlTobase64(this.imgList[0])
				}
			});
			 
		},
		
		
		
		urlTobase64(url){
		    uni.request({
			url: '',
			method:'GET',
			responseType: 'arraybuffer',
			success: ress => {
				console.log(ress)
				let base64 = wx.arrayBufferToBase64(ress.data); //把arraybuffer转成base64 
				base64 = 'data:image/jpeg;base64,' + base64 //不加上这串字符，在页面无法显示的哦
				console.log(base64)
				this.dataURLToBlob(base64)
				/* let blob= this.dataURLtoFile(base64,'image/jpeg');
				console.log(blob) */
				console.log(this.blob)
			}
		    })
		},
		
		dataURLToBlob(dataurl){
		    var arr = dataurl.split(',');
		    var mime = arr[0].match(/:(.*?);/)[1];
		    var bstr = atob(arr[1]);
		    var n = bstr.length;
		    var u8arr = new Uint8Array(n);
		    while(n--){
		        u8arr[n] = bstr.charCodeAt(n);
		    }
			
			this.blob = new Blob([u8arr], {type:mime})
		    return new Blob([u8arr], {type:mime});
		},
		
		dataURLtoFile(dataURI, type) {
		  let binary = atob(dataURI.split(',')[1]);
		  let array = [];
		  for(let i = 0; i < binary.length; i++) {
		    array.push(binary.charCodeAt(i));
		  }
		  return new Blob([new Uint8Array(array)], {type:type });
		},
		
		//预览图片
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		//删除图片
		DelImg(e) {
			uni.showModal({
				title: '删除照片',
				content: '确定要删除这张照片吗？',
				cancelText: '取消',
				confirmText: '确定',
				success: res => {
					if (res.confirm) {
						this.imgList.splice(e.currentTarget.dataset.index, 1)
					}
				}
			})
		},
		
		
		radioChange(e) {
			console.log(e)
			var checked = e.target.value
			var changed = {}
			this.type = e.target.value
			if(this.type === '退货'){
				this.type = 1
			}else{
				this.type = 2
			}
			for (var i = 0; i < this.radioItems.length; i++) {
				if (checked.indexOf(this.radioItems[i].name) !== -1) {
					changed['radioItems[' + i + '].checked'] = true
				} else {
					changed['radioItems[' + i + '].checked'] = false
				}
			}
			
		},
		activeFc(res) {
			console.log(JSON.stringify(res));
			uni.showToast({
				title: '获取输入成功'
			})
		},
		bindChange(res) {
			this.count = Number(res)
			console.log('返回数据',res);
			
		},
		textInput(e){
			console.log(e)
		},
		async doSubmit(e){
			await uniRequest({
				url:'order/create/return/goods/user/',
				method:'post',
				data: {
					order_id:this.orderId,
					goods_id:this.goodsItem.sku,
					count:this.count,
					cause:this.desc,
					image1:this.imgList[0],
					image2:this.imgList[1],
					image3:this.imgList[2],
					image4:this.imgList[3],
					service_type:this.type
				},
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token,
				},
			}).then(res=>{
				if(res.status === 200){
					this.$api.msg('提交成功')
					console.log(res)
					/* setTimeout(function(){
						uni.navigateBack()
					},300) */
				}else{
					this.$api.msg(res.data.non_field_errors[0])
				}
			}).catch(error=>{
				console.log(error)
			})
		}
	}
}
